.navbar {

    background: transparent;
    width: 100%;
    height: 80px;
    position: fixed;
    top: 0;
    z-index: 10;
    display: flex;
    align-items: center;
    transition: top 0.3s ease-in-out, background 0.3s ease-in-out;
}

.navbar.scrolled {
    background: rgba(0, 0, 0, 0.7); /* Darken the background when scrolled */
    transition: background 0.3s ease-in-out;
}

.navbar-container {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    height: 80px;
    width: 100%;
}

.nav-menu {
    display: flex;
    list-style: none;
    text-align: left;
    justify-content: flex-end;
    margin-right: 5rem;
    flex: 1;
    padding: 0;
}

.nav-item {
    cursor: pointer;
    height: 35px;
    color: white;
    display: flex;
    align-items: center;
    text-decoration: none;
    padding: 0.5rem 1rem;
    font-size: 1rem;
    background-color: rgba(104, 104, 104, 0);
    transition: background-color 0.2s ease-out, opacity 0.2s ease-out;
    border-radius: 10px;
}

.nav-item:hover {
    background-color: rgba(119, 119, 119, 0.8);
    transition: all 0.2s ease-out;
}
